    "darken-1" : #0c86c5, 
    "darken-2" : #2c77ca, 
    "darken-3" : #1e3a6c, 
    "darken-4" : #0f2a56, 
    "darken-5" : #0f203e, 
);

$green: (
    "base" : #86b755, 
    "lighten-5" : #E8F5E9, 
    "lighten-4" : #C8E6C9, 
    "lighten-3" : #A5D6A7, 
    "lighten-2" : #81C784, 
    "lighten-1" : #66BB6A, 
    "darken-1" : #43A047, 
    "darken-2" : #388E3C, 
    "darken-3" : #2E7D32, 
    "darken-4" : #1B5E20, 
    "accent-1" : #B9F6CA, 
    "accent-2" : #69F0AE, 
    "accent-3" : #00E676, 
    "accent-4" : #00C853
