$red: (
    "base" : #F44336, 
    "lighten-5" : #FFEBEE, 
    "lighten-4" : #FFCDD2, 
    "lighten-3" : #EF9A9A, 
    "lighten-2" : #E57373, 
    "lighten-1" : #EF5350, 
    "darken-1" : #E53935, 
    "darken-2" : #D32F2F, 
    "darken-3" : #C62828, 
    "darken-4" : #B71C1C, 
    "accent-1" : #FF8A80, 
    "accent-2" : #FF5252, 
    "accent-3" : #FF1744, 
    "accent-4" : #D50000);

$grey: (
    "base" : #504d50, 
    "darken-1"  : #30383b,
    "lighten-5" : #eeeeee, 
    "lighten-4" : #e6e7e8, 
    "lighten-3" : #d3d4d5, 
    "lighten-2" : #bfbfc0,
    "lighten-1" : #787a7a
    );

$processing: (
    "base"              : #d4cba4, 
    "darken-1"          : #c2b98e, 
    "darken-2"          : #9d906f
);

$paintshop: (
    "base"              : #9b64aa,
    "darken-1"          : #895aa4,
    "darken-2"          : #5b266c
);

$data-cleaning: (
    "base"              : #0ea285,
    "darken-1"          : #108673,
    "darken-2"          : #006654   
 );
 $mobility: (
    "base"              : #eeaa32,
    "darken-1"          : #be8b37,
    "darken-2"          : #ae7d36    
 );
 $washing: (
    "base"              : #9ad9eb,
    "darken-1"          : #68b2c6,
    "darken-2"          : #43888d    
 );
 $environment: (
    "base"              : #a4d06e,
    "darken-1"          : #78a94e,
    "darken-2"          : #577d40    
 );
 $oil-gas: (
    "base"              : #52656e,
    "darken-1"          : #294146,
    "darken-2"          : #001d27    
 );

$shades: (
    "black" : #000000,
    "white" : #FFFFFF, 
    "transparent" : transparent
);

$colors-ess: (
    "essblue": $essblue, 
    "red": $red, 
    "green": $green, 
    "grey": $grey, 
    "shades": $shades, 
    "processing":$processing, 
    "data-cleaning": $data-cleaning, 
    "paintshop": $paintshop, 
    "mobility": $mobility,
    "washing": $washing,
    "environment": $environment,
    "oil-gas": $oil-gas
);

@function color($color, $type) {
    @if map-has-key($colors-ess, $color) {
        $curr_color: map-get($colors-ess, $color);

        @if map-has-key($curr_color, $type) {
            @return map-get($curr_color, $type);
        }
    }

    @warn "Unknown `#{$color}` - `#{$type}` in $colors.";
    @return null;
}




// COLOR CLASSES ---------------------------------------------------


@each $color_name, $color in $colors-ess {

    @each $color_type, $color_value in $color {
        @if $color_type=="base" {
            .#{$color_name} {
                background-color: $color_value;
            }
            .has-#{$color_name}-background-color {
                background-color: $color_value !important;
            }

            .#{$color_name}-color {
                color: $color_value;
            }
            .outlined-#{$color_name} {
                color: $color_value !important;
                border: 1px solid $color_value !important;
            }
            .ess-dark-hover-link:hover .outlined-#{$color_name} {
                background-color: $color_value !important;
                color: white !important;
            }
        }

        @else {
            .#{$color_name}.#{$color_type} {
                background-color: $color_value;
            }

            .#{$color_name}-text.text-#{$color_type} {
                color: $color_value;
            }
        }
    }
}


// SHADES CLASSES ---------------------------------------------------


@each $color, $color_value in $shades {
    .#{$color} {
        background-color: $color_value !important;
    }

    .#{$color}-text {
        color: $color_value !important;
    }
}


// GREY CLASSES ---------------------------------------------------


@each $color, $color_value in $grey {

    .has-grey-#{$color}-background-color {
        background-color: $color_value !important;
    }

    .grey-#{$color}-text-color {
        color: $color_value !important;
    }
}


// MIXINS FOR GRADIENT AND OPACITY -------------------------------


@mixin opacity($value) {
    opacity: $value;
}

@mixin radial-gradient($outer_color, $inner_color) {
    background: radial-gradient($outer_color, $inner_color);
}
