$csscube-h1 : calc(1rem + 1.8vw + 1.8vh + .7vmin);
$csscube-h2 : calc(0.8rem + 0.5vw + 0.5vh + .3vmin);
$csscube-h3 : calc(0.6rem + 0.3vw + 0.3vh + .25vmin);
$csscube-p  : calc(.4rem + .2vw + .2vh + .2vmin);

$alpha-active   : 0.9;
$alpha-inactive : 0.1;


#el-clickable-background {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: $light-gray;
    background: $nav-cube-background;
    //opacity: 0.99;
}

#ess-cube-navigation-holder {
    display: flex;
    flex-flow: row wrap;
    justify-items: stretch;
    align-items: baseline;
    align-content: baseline;
    z-index: -100;
    opacity: 0;
    position: fixed;
    width: 100vw;
    height: 100vh;
    
    #breadcrub-cont {
        flex: 1 1 25%;
        position: absolute;
    }
    #right-part {
        flex: 1 1 25%;
    };
    #csscube-nav {
        flex-basis: 100%;
        max-width: 100%;
        margin: 0;
        padding: 0;
        z-index: 10000;
        align-self: baseline;
        position: relative;
        top: 0;
        border-bottom: 1px solid color("grey", "lighten-2");
    
        ul {
            margin: 0;
            padding: 0;
            text-align: center;
            li {
				&::before {
					display: none;
				}
                margin: 0;
                padding: 0; 
                display: inline-block;

                &+li {
                    border-left: 1px solid color("grey", "lighten-2");
                }
                a {
                    display: block;
                    padding: 0 $base-margin;
                    cursor:pointer;
                    color: color("grey", "base");
                    transition: background .5s ease, color .5s ease, border .5s ease;
                    border: 1px solid rgba(0,0,0,0);
                    height: 4rem;
                    line-height: 4rem;

                    &:hover {
                        text-decoration: none;
                        //background: color('grey', 'lighten-2')-light-iii;
                        color: color("grey", "base");
                    }

                    &.active {
                        color: color("grey", "base");
                        //background: color('grey', 'lighten-2')-light-iii;
                    }
                }
            }
        }
        
    }

}

@keyframes navrot {
    to {
        transform: rotateY(360deg)
    }
}

.css_block {

    //border: 0.5px solid color("grey", "base");

    a {
        padding: $half-margin;
    }

    &.css-grey>div, &.css-grey>a {
        background: color("grey", "lighten-2");
        &:hover {
            background: color("grey", "lighten-3");
        }
    }
    &.css-supplementary>a {
        background: $secondary-color;
    }
    &.css-gradient>a{
        background: $ess-blue-gradient;
    }

    &.css-white>div, &.css-white>a {
        background: $white;
        &:hover {
            background: $primary-color;
        }
    }
    &.css-white>div, &.css-grey-lighter>a {
        background: color('grey', 'lighten-5');
        &:hover {
            background: $white;
        }
    }

    &.css-dark-blue>div, &.css-dark-blue>a{
        background: $secondary-color;
    }

    &.css-color-blue>div, &.css-color-blue>a {
        color: #87c8ff;
    }

    &.css-color-white>div, &.css-color-white>a {
        color: $white !important;
        h2 {color: $white !important;}
    }

    &.css-color-dark-grey>div, &.css-color-dark-grey>a {
        color: $primary-text;
    }


    position: absolute;
    overflow: hidden !important;
}

#csscube-cont {

    //position: relative;
    flex-basis: 100%;
    max-width: 100%;
    align-self:baseline;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: stretch;


    #csscube-scene {
        z-index: 10;
        opacity: 0;
        
        position: absolute;
        margin: auto; // centers the cube horizontaly;

        #csscube-rotator {
            transform: rotateX(-7deg);
            z-index:10;
            position: relative;
            //top: 50%;
            transform-style: preserve-3d;
            transform-origin: 0% 0% 50%;
            -moz-transform-origin: 0% 0% 50%;
            -webkit-transform-origin-z: 50%;

            #csscube {

                transform-style: preserve-3d;

                position: relative;
                //top: -300px;

                h1 {
                    top: 0;
                    left: 0;
                    font-family: 'Exo';
                    font-weight: light;
                    line-height: 0.8;
                    text-align: left;
                }

                h2 {
                    text-align: center;
                    padding: &half-margin;
                    font-weight: 400;
                }
                h3 {
                    padding: $half-margin;
                    text-align: center;
                }               

                #csscube-front,
                #csscube-left,
                #csscube-right,
                #csscube-back,
                #csscube-top,
                #csscube-bottom {

                    background: transparent;
                    position: absolute;
                    top: 0;
                    left: 0;
                    backface-visibility: visible;

                    .css_block {
                        display: flex;
                        align-items: stretch;

                        &.css-block-header {
                            &>div{

                                &:hover {
                                    cursor: default;
                                    background-color: inherit;
                                }
                            }
                        }
                        &.cbl-industry {
                            
                            &>a { 
                                align-content: stretch; 
                                padding-bottom: $base-margin; 

                                /*&:hover {  // HOVER ON ICONS
                                    .cc-icon-h {
                                        .ess-anim-icon {
                                            fill: color('essblue', 'lighten-4');
                                            stroke: color('essblue', 'base');
                                        }
                                    } 
                                }*/
                                .cc-icon-h {
                                    align-self: flex-end;
                                    width: 100%;
                                    padding: 0 20%;
                                    
                                    .ess-anim-icon {
                                        fill: color('grey', 'lighten-3');
                                        stroke: color('grey', 'base');
                                    }
                                }

                                h2 {
                                    align-self: flex-end;
                                }
                            }
                        }

                        &>div, &>a {
                            clip-path: polygon(0 .3rem, 0 calc( 100% - .3rem ), .3rem 100%, calc( 100% - .3rem ) 100%, 100% calc( 100% - .3rem ), 100% .3rem, calc( 100% - .3rem ) 0, .3rem 0);
                            transform-style: preserve-3d;
                            text-decoration: none;
                            color: $secondary-color;
                            flex: 1;
                            display: flex;
                            align-items: center;
                            align-content: center;
                            justify-content: center;
                            flex-wrap: wrap;
                            position: rel ative;
                            margin: .05rem;
                            padding: $base-margin;
                            overflow: hidden;
                            transition: all .5s ease;
                                * {margin: 0; padding: 0;}
                                //margin: 4px;
                                &:hover {
                                    text-decoration: none;
                                    cursor: pointer;
                                    //transform: translateY(-.5rem);
                                    //scale: 1.1;
                                    //background-color: $primary-color;
                                    opacity: 1 ;
                         
                                }
                                .icon-image {
                                    padding: 0 $half-margin;
                                }
                                &.hover-dark:hover {
                                    background: $secondary-color;
                                }

                                &.background-image-link {
                                    img {
                                        position: absolute; top: 0; opacity: 0;  transition: opacity .5s ease;
                                    }
                                    &:hover img {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
            }
        }
        #csscube-shadow {
            background: color("grey", "base");
            opacity: 0.1;
            filter: blur(30px);
            z-index: 1;
            transform-style: preserve-3d;
        }
        .csscube-navbutton {
            position: absolute;
            cursor: pointer;
            background: none;
            z-index: 1000;
            border: none;
            top: calc( 40% );
            
            .ess-icon {
                font-size: calc(5vw + 5vh);
                transition: color .5s ease;
                color: $primary-color;

            }
            &.left-button {
                 left: calc(-1.5*(5vw + 5vh));   
            }
            &.right-button {
                right: calc(-1.5*(5vw + 5vh));
            }
            &:hover {
                .ess-icon {
                    color: color('essblue', 'darken-1');
                }
            }
            
        }
    }
}
#x-button,
#left-button,
#right-button {
    width: 70px;
    height: 70px;
    background: none;
    border: none;
    z-index: 10000;

    &:hover {
        background: black;
    }

    top: 300px;
    position: fixed;
}

#left-button {
    left: -120px;
}

#right-button {
    right: 0px;
}
#x-button {left: 50%; top: auto; bottom: 0;}

// CSS ESS CUBE - Small logos inside Product panels of the Cube

#csscube-cont #csscube-scene #csscube-rotator #csscube #csscube-back .product-block {
    a {
        flex-direction: column;
        flex-wrap: nowrap;
    }
    a:hover {
        background-color: color('grey', 'lighten-4');
    }
    h3 { margin-top: -.2em; }
    .svg-ess-cube { height: 25%; margin-bottom: 0; }
    
    &.main-product-block{
        h3 { font-weight: bold; }
        a {}
        a:hover {
             background: color('grey', 'lighten-4');
        }
    }
    .main-product-logo { height: 50%; margin-bottom: 0; }
}


