html,
body {
	background-color: color("shades", "white");
	color: color ("gray", "darken-3");
	font-size: 100%;
}

#ess-body {
	overflow-x: hidden;

	.ess-main {
		padding: $quad-margin 0 $double-margin 0;
	}
}

a:hover {
	text-decoration: underline;
}


@mixin ess-fade($value) {
	-webkit-transition: $value;
	-ms-transition: $value;
	-o-transition: $value;
	transition: $value;
}

@mixin shadow($value) {
	-moz-box-shadow: $value;
	-webkit-box-shadow: $value;
	box-shadow: $value;
}

@mixin max-width-image() {
	max-width: 100%;
	width: auto;
	height: auto;
}

/* ============ margins and spaces ===================== */


.w-100 {
	width: 100%;
}

.h-100 {
	height: 100%;
}

.force-margin {
	padding: 0 $base-margin !important;
}

.ess-padding {
	padding: $base-margin;
}

.ess-padding>* {
	padding: 0;
	margin: 0;
}

.no-padding {
	padding: none;
}

#ess-body {

	.p-bot-base {
		padding-bottom: $base-margin !important;
	}

