	}

	.p-top-quad {
		padding-top: $quad-margin !important;
	}

	.p-top-half {
		padding-top: $half-margin !important;
	}

	.p-top-0 {
		padding-top: 0 !important;
	}

	.p-all-base {
		padding: $base-margin !important;
	}

	.p-all-double {
		padding: $double-margin !important;
	}

	.p-all-half {
		padding: $half-margin !important;
	}

